.review_text {
  color: #777;
  margin: 40px 0px;
  line-height: 30px;
}
.slider_content img {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  object-fit: cover;
}
@media only screen and (max-width: 768px) {
  .review_text{
    font-size: 0.8rem;
    margin: 20px 0px;
  }
  .slider_content img{
    width: 35px;
    height: 35px;
  }
  .slider_content h6{
    font-size: 0.8rem;
  }
 } 