.header {
  width: 100%;
  height: 100px;
  line-height: 100px;
  top: 0;
}

@-webkit-keyframes animation {
  from {
    top: -100px;
  }
  to {
    top: 0px;
  }
}

@keyframes animation {
  from {
    top: -100px;
  }
  to {
    top: 0px;
  }
}

.header__shrink {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  background: #fff;
  z-index: 99999;
  box-shadow: 5px 5px 15px -5px #d7d6d6;
  -webkit-animation-name: animation;
          animation-name: animation;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.logo {
  text-align: center;
}

.logo:hover {
  cursor: pointer;
}

.logo img {
  width: calc(100% - 60%);
  object-fit: contain;
  margin-top: -40px;
}

.logo h5 {
  font-weight: 600;
  font-size: 1rem;
  color: #212245;
  margin-top: -40px;
  margin-bottom: 0;
}

.menu a {
  font-weight: 600;
  font-size: 1rem;
  color: #212245;
  transition: 0.3s;
}

.menu a:hover {
  color: #df2020;
}

.active__menu {
  color: #df2020 !important;
}

.cart__icon {
  position: relative;
}

.cart__icon i,.user i,.mobile__menu i{
  font-size: 1.3 rem;
  color: #212245;
  cursor: pointer;
}


.cart__badge {
  position: absolute;
  top: 30px;
  right: -10px;
  background: #df2020;
  color: #fff;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  font-size: 0.8rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mobile__menu {
  font-weight: 600;
  display: none;
}

.header__shrink {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  background: #fff;
  z-index: 99999;
  box-shadow: 5px 5px 15px -5px #fde4e4;
}
@media only screen and (max-width: 992px) {
  .logo img {
    width: calc(100% - 70%);
  }
  .logo h5 {
    font-size: 0.8rem;
    margin-top: -15px;
  }

  .navigation {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.425);
    z-index: 99;
    display: none;
  }

  .menu {
    position: absolute;
    top: 0;
    right: 0;
    width: 250px;
    height: 100%;
    background: #fff;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 100;
    line-height: 20px;
  }

  .menu a {
    font-size: 0.9rem;
    color: #212245;
    transition: 0.3s;
  }

  .menu a:hover {
    color: #df2020;
  }

  .mobile__menu {
    display: block;
  }

  .cart__icon i,.user i,.mobile__menu i{
    font-size: 1 rem;
    color: #212245;
    cursor: pointer;
  }

  .show__menu {
    display: block;
  }
}

